
.slick-arrow {
    position: absolute;
    display: block;
    height: 45px;
    width: 45px;
    line-height: 0;
    font-size: 40px;
    cursor: pointer;
    top: 50%;
    margin-top: -10px;
    padding: 0;
    border: 0;
    outline: none;
    background: whitesmoke;
    border-radius: 50%;
}
.next {
    right: -50px;
}
.prev {
    left: -50px;
}